.stopwatch {
  background-color: #eee;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__center {
  background-color: #000;
  border-radius: 50%;
  width: 1.6vmin;
  height: 1.6vmin;
  position: absolute;
  top: 39.2vmin;
  left: 39.2vmin;
}

.stopwatch__minutes {
  transform-origin: bottom;
  background-color: #00f;
  border-radius: 8px;
  width: .5vmin;
  height: 32vmin;
  animation-name: minutes;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  top: 8vmin;
  left: 39.75vmin;
}

.stopwatch__seconds {
  transform-origin: 50% 100%;
  background-color: red;
  border-radius: 8px;
  width: .5vmin;
  height: 36vmin;
  animation-name: seconds;
  animation-duration: 5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  top: 4vmin;
  left: 39.75vmin;
}

@keyframes seconds {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes minutes {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.container {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

